#sidebar {
    box-sizing: border-box;
    height: 100vh;
    width: 15%;
    padding: 5px 1em;
    background-color: #239afc;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    display: flex;
}

#sidebar ul {
    list-style: none;
}

#sidebar a {
    border-radius: .5em;
    padding: .85em;
    text-decoration: none;
    color: antiquewhite;
    display: flex;
    align-items: center;
    gap: 2em;

}

#sidebar a:hover {
    color: #282c34;
    background-color: #76c1ff;
}

@media screen and (max-width:1500px){
    #sidebar{
        display: inline;
        height: 10vh;
        width: 100%;
        top: 0;
        left: 0;
        right: 0;
    }

    #sidebar li{
        display: inline;

    }

    #sidebar a {
        display: inline;
    }
}